                                   <b>Nachiarkoil Kuthuvilakku - Nachiarkoil Lamp </b><br><br>
                                   
The Nachiarkoil lamp, also called Annam lamp or Nachiarkoil Kuthuvilakku, is an ornamental brass lamp made of series of diyas, a handicraft product which is exclusively made by Pather (Kammalar) community in Nachiyar Koil town in Tamil Nadu, Nachiarkoil Lamp is made in Nachiarkoil, a town in Kumbakonam taluk in the Tanjore district of Tamil Nadu.<br><br>

The town is about 6 miles (9.7 km) southeast of Kumbakonam on the Kumbakonam – Tiruvarur main road. <br><br>

The lamps are made by resident artisans called Pathers. The Nachiarkoil lamp is made both in bell metal and brass; brass is more in vogue now since it is easily available and cheaper. <br><br>

The lamp has four components which are: parts—base "Keezhbagam (base plate), "kandam" meaning pivotal post, "Thanguli" a bowl with five groves to hold wicks and oil for lighting, and the "Prabhai" which is the crown. <br><br>

The lamps that are made in a standing form or a pedestal form are given the prefix of Nachiarkoil. <br><br>

Other forms of lamps are hung from the ceiling in temples or "deepams". <br><br>

These lamps are extensively ornamented.<br><br>
